@import "variables";
@import "mixin";
@import "main_drop";

body{
  font-size: 18px;
  line-height: 20px;
  color: #474747;
  .header {
    width: 100%;
    position: absolute;
    top: 0;
    .containerParent();
    .container {
      .containerChild();
      display: grid;
      grid-template-areas: ". headline headline"
                           "logo search cabinet"
                           "navPanel navPanel navPanel"
                           "secondNav secondNav secondNav";
      grid-template-columns: 25% 45% 30%;
      .header__headline {
        grid-area: headline;
        p {
          font: 1em/30px @sansregular;
          color: #474747;
          text-align: center;
          background-color: #ececec;
          border-bottom-right-radius: 10px;
          border-bottom-left-radius: 10px;
        }
      }
      .header__logo {
        grid-area: logo;
        a{
          display: block;
          width: 100%;
          height: 100%;
          background: url("../img/logo.png") no-repeat 50% 100%;
          background-size: contain;
        }
      }
      .header__mainSearch {
        grid-area: search;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        align-items: center;
        .search {
          width: 100%;
          margin-top: 70px;
          text-align: center;
          position: relative;
          input {
            width: 95%;
            height: 45px;
            padding-left: 25px;
            font: 1.125em/45px @sansregular;
            color: #9e9e9e;
            border: 3px solid @mainColor;
            border-radius: 50px;
            background: transparent;
            outline: none;
          }
          i {
            font-size: 1.5em;
            position: absolute;
            right: 20px;
            top: 13px;
            color: @mainColor;
          }
        }
        .numbers {
          margin: 40px 0 0;
          width: 70%;
          display: flex;
          align-items: center;
          .icon-auricular-phone-symbol-in-a-circle{
            display: none;
          }
          ul {
            width: 100%;
            display: flex;
            justify-content: space-around;
            align-items: center;
            li {
              .icon-auricular-phone-symbol-in-a-circle {
                display: block;
                font-size: 3em;
                color: @mainColor;
              }
              a {
                font: 1.625em/2em @sansregular;
                color: #474747;
                text-decoration: none;
              }
            }
          }
        }
        .workTime {
          margin: 55px 0 0;
          width: 30%;
          display: flex;
          justify-content: flex-end;
          i {
            font-size: 1.5em;
            line-height: 1.725em;
            color: @mainColor;
          }
          ul {
            margin-left: 20px;
            li {
              span {
                font: .75em/1em @sansregular;
                white-space: nowrap;
              }
            }
          }
        }
      }
      .header__cabinet {
        grid-area: cabinet;
        margin-top: 70px;
        ul {
          display: flex;
          flex-wrap: wrap;
          justify-content: space-between;
          li {
            a {
              display: flex;
              justify-content: space-around;
              align-items: center;
              text-decoration: none;
              span {
                display: inline-block;
                font: 1.125em/1.125em @sansregular;
                color: #474747;
                &:nth-child(1):hover{
                  text-decoration: none;
                }
                &:nth-child(2){
                  width: 100%;
                  text-align: center;
                }
                i {
                  padding: 0 20px;
                  font-size: 3em;
                  line-height: 1.125em;
                  color: @mainColor;
                }
                &:hover {
                  text-decoration: underline;
                  cursor: pointer;
                }
              }
            }
            &:nth-child(1) {
              width: 45%;
              margin: 0 0 0 10px;
            }
            &:nth-child(2) {
              width: 45%;
            }
            &:nth-child(3) {
              width: 100%;
              display: flex;
              justify-content: flex-end;
              a{
                margin-top: 50px;
                padding: 5px;
                width: 75%;
                display: flex;
                justify-content: flex-start;
                align-items: center;
                font: 1.125em/1.1em @sansregular;
                color: #474747;
                text-align: center;
                text-decoration: none;
                border: 1px solid @mainColor;
                border-radius: 5px;
                span{
                  i{
                    margin: 0 0 20px;
                    padding: 0;
                    font-size: 2em;
                    color: #7d3daf;
                    border-radius: 5px;
                  }
                }
              }
            }
          }
        }
      }
      .header__nav-panel {
        grid-area: navPanel;
        margin-top: 25px;
        display: flex;
        flex-direction: column;
        .mobileDrop{
          display: none;
        }
        &__major {
          ul {
            display: flex;
            justify-content: space-between;
            li {
              width: 16.67%;
              a {
                display: inline-block;
                position: relative;
                width: 100%;
                font: 1.125em/50px @sansbold;
                color: #fff;
                text-align: center;
                text-decoration: none;
                background-color: @fontColor;
                border-top: 1px solid @fontColor;
                &:hover {
                  transition: .5s;
                  background-color: @hoverColor;
                }
              }
              &:nth-child(1){
                a{
                  border-left: 1px solid @fontColor;
                  border-right: 1px solid #77c9cc;
                }
              }
              &:nth-child(2){
                a{
                  border-left: 1px solid #77c9cc;
                  border-right: 1px solid #77c9cc;
                }
              }
              &:nth-child(3){
                a{
                  border-left: 1px solid #77c9cc;
                  border-right: 1px solid #77c9cc;
                }
              }
              &:nth-child(4){
                a{
                  border-left: 1px solid #77c9cc;
                  border-right: 1px solid #77c9cc;
                }
              }
              &:nth-child(5){
                a{
                  border-left: 1px solid #77c9cc;
                  border-right: 1px solid #77c9cc;
                }
              }
              &:nth-child(6){
                a{
                  border-left: 1px solid #77c9cc;
                  border-right: 1px solid @fontColor;
                }
              }
            }
            .footer{
              display: none;
              height: 100vh;
              background-color: @fontColor;
              border: 1px solid #77c9cc;
            }
          }
        }
        &__goods {
          ul {
            display: flex;
            justify-content: space-between;
            li {
              width: 25%;
              a {
                display: flex;
                justify-content: center;
                align-items: center;
                width: 100%;
                font: 1.125em/50px @sansregular;
                color: @linkColor;
                text-decoration: none;
                background-color: #77c9cc;
                border: 1px solid @fontColor;
                .active{
                  background: @hoverColor;
                }
              }
              &:hover a{
                color: #fff;
                background-color: @hoverColor;
                transition: .5s;
              }
            }
          }
        }
      }
    }
  }
}